.container {
    padding-top: 62px;
}

.text {
  margin-top: 0;
  color: #fff;
  text-align: center;
  text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
  font-size: 56px;
  font-weight: 700;
  text-transform: uppercase;

  margin-bottom: 16px;
}

.btn {
    display: block;
    margin-left: auto;
    margin-right: auto;
  width: 220px;
  height: 48px;
  border: none;
  border-radius: 8px;
  background: linear-gradient(0deg, #f3f3f3 0%, #fff 100%);

  box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.2);
  
color: #3B4262;
font-size: 16px;
font-weight: 600;
letter-spacing: 2.5px;
text-transform: uppercase;
font-family: Barlow Semi Condensed;

}
