.score-container {
  width: 80px;
  height: 72px;
  border-radius: 4px;
  background: linear-gradient(0deg, #f3f3f3 0%, #fff 100%);
  box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
}

.score-text {
color: #2A45C2;
text-transform: uppercase;
text-align: center;
font-size: 10px;
font-weight: 600;
letter-spacing: 1.563px;
}

.score-current {
display: block;
color: #565468;
text-align: center;
font-size: 40px;
font-weight: 700;
line-height: 40px; 
}
